* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.findID {
  width: 100%;
  height: 100vh;
  font-family: 'Segoe UI', sans-serif;
  background-image: url(../image/ground.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.findID::before {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgba(225, 247, 236, 0.8); /* 민트 오버레이 */
  backdrop-filter: blur(4px);
  z-index: 0;
}

.img {
  position: absolute;
  top: 30px;
  left: 30px;
  background-image: url(../image/back.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  cursor: pointer;
  z-index: 2;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.IDform {
  position: relative;
  z-index: 1;
  width: 500px;
  padding: 40px;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.IDarea {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin-bottom: 30px;
  font-size: 26px;
  color: var(--text-color);
}

.name,
.number,
.verifyCode {
  width: 100%;
  height: 45px;
  margin-bottom: 15px;
  padding: 0 15px;
  border: 2px solid var(--accent-color);
  background-color: var(--card-bg);
  border-radius: 8px;
  font-size: 14px;
  color: var(--text-color);
  transition: 0.3s ease;
}

.name:focus,
.number:focus,
.verifyCode:focus {
  outline: none;
  border-color: var(--accent-color);
  background-color: var(--card-bg);
  box-shadow: 0 0 0 2px rgba(44, 148, 126, 0.3);
}

::placeholder {
  color: #94a3b8;
  font-size: 13px;
}

.sendBtn,
.verifyBtn,
.findBtn {
  width: 100%;
  height: 45px;
  margin-top: 10px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  background-color: var(--accent-color);
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(44, 148, 126, 0.3);
}

.sendBtn:hover,
.verifyBtn:hover,
.findBtn:hover {
  background-color: var(--accent-color);
  transform: translateY(-2px);
}

.result {
  margin-top: 30px;
  padding: 20px;
  background-color: var(--card-bg);
  border-radius: 12px;
  text-align: center;
  color: var(--text-color);
  box-shadow: inset 0 0 4px var(--hover-bg);
}

.goLogin {
  margin-top: 15px;
  padding: 12px 30px;
  background-color: var(--accent-color);
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 8px rgba(44, 148, 126, 0.3);
}

.goLogin:hover {
  background-color: var(--accent-color);
  transform: scale(1.05);
}
